<template>
  <div class="container">
      <ods-row :gutter="16">
        <ods-col :xs="24" :sm="24" :md="24" :lg="12" :xl="12">
          <ods-module class="fixed-card">
            <ods-row slot="header" class="border">
              <h1 class="h1">{{$t('dashboard.alerts')}}</h1>
            </ods-row>
            <ods-row>
              <ods-row>
                <ods-table :data="alerts" height="290" class="table-style" size="mini">
                  <ods-table-column
                    :label="$t('alert.state')"
                    width="70">
                    <template slot-scope="scope">
                      <ods-tooltip effect="dark" :content="scope.row.state" placement="top">
                        <div :class="scope.row.state"></div>
                      </ods-tooltip>
                    </template>
                  </ods-table-column>
                  <ods-table-column
                    prop="date"
                    :label="$t('alert.date')"
                    width="100">
                  </ods-table-column>
                  <ods-table-column
                    prop="alertType"
                    :label="$t('alert.alertType')">
                  </ods-table-column>
                  <ods-table-column
                    prop="mac"
                    :label="$t('alert.mac')">
                  </ods-table-column>
                  <ods-table-column
                    prop="devType"
                    :label="$t('alert.devType')"
                    width="100">
                  </ods-table-column>
                </ods-table>
              </ods-row>
            </ods-row>
          </ods-module>
      </ods-col>
      <ods-col :xs="24" :sm="24" :md="24" :lg="12" :xl="12">
        <ods-module class="fixed-card">
          <ods-row slot="header" class="border">
            <h1 class="h1">{{$t('dashboard.volume')}}</h1>
          </ods-row>
          <ods-row :gutter="10" class="line-height">
            <ods-col :xs="13" :sm="13" :md="13" :lg="13" :xl="13">
              <ods-row style="margin-bottom: 20px;">
                <ods-col :xs="1" :sm="1" :md="1" :lg="1" :xl="1" :offset="2">
                  <div class="punto_rojo"></div>
                </ods-col>
                 <ods-col :xs="9" :sm="9" :md="9" :lg="9" :xl="9">
                  <h1 class="legend">{{$t('dashboard.devicesKO')}}</h1>
                </ods-col>
                <ods-col :xs="1" :sm="1" :md="1" :lg="1" :xl="1" :offset="2">
                  <div class="punto_verde"></div>
                </ods-col>
                 <ods-col :xs="9" :sm="9" :md="9" :lg="9" :xl="9">
                  <h1 class="legend">{{$t('dashboard.devicesOK')}}</h1>
                </ods-col>
              </ods-row>
              <ods-row  class="gauge-row" type="flex" justify="space-between">
                <ods-col class="gauge-col">
                    <ods-chart v-if="porcentaje" type="gauge" :data="porcentaje" :size="230" :text="true" symbol="%"></ods-chart>
                </ods-col>
              </ods-row>
            </ods-col>
            <ods-col :xs="10" :sm="10" :md="10" :lg="10" :xl="10">
                <ods-row :gutter="20" class="margin-top-50">
                  <ods-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" :span="12">
                    <ods-row :gutter="10" class="border-bottom">
                      <ods-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
                        <h2 class="h2"><ods-icon class="margin-right-9" name="broadcast" size="16" color="#6E767D"/>{{$t('dashboard.devices')}}</h2>
                      </ods-col>
                      <ods-col :xs="12" :sm="12" :md="12" :lg="12" :xl="12">
                        <div class="padding-top-bottom">
                          <label class="number">{{nDevices}}</label>
                        </div>
                        <h2 class="legend">{{$t('dashboard.totals')}}</h2>
                      </ods-col>
                      <ods-col :xs="12" :sm="12" :md="12" :lg="12" :xl="12">
                        <div class="padding-top-bottom">
                          <label class="number red">{{nDevicesKO}}</label>
                        </div>
                        <h2 class="legend red">{{$t('dashboard.witherror')}}</h2>
                      </ods-col>
                    </ods-row>
                  </ods-col>
                  <ods-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" :span="12">
                    <ods-row :gutter="10" class="padding-top-bottom">
                      <ods-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
                        <h2 class="h2"><img class="margin-right-9 margin-img" src="static/icon_senales.svg"/>{{$t('dashboard.attributes')}}:</h2>
                      </ods-col>
                      <ods-col :xs="12" :sm="12" :md="12" :lg="12" :xl="12">
                        <div class="padding-top-bottom">
                          <label class="number">{{nSignalsReportables}}</label>
                        </div>
                        <h2 class="legend">{{$t('dashboard.totals')}}</h2>
                      </ods-col>
                      <ods-col :xs="12" :sm="12" :md="12" :lg="12" :xl="12">
                        <div class="padding-top-bottom">
                          <label class="number green">{{nAttOK}}</label>
                        </div>
                        <h2 class="legend green">{{$t('dashboard.received')}}</h2>
                      </ods-col>
                    </ods-row>
                  </ods-col>
                </ods-row>
            </ods-col>
          </ods-row>
        </ods-module>
      </ods-col>
    </ods-row>
    <ods-row >
      <ods-col>
        <ods-module class="graph-card" v-loading="loading">
          <ods-row slot="header" class="structure-header header-graph">
            <ods-row class="border">
              <ods-col :span="8" :offset="8">
                <div class="header-graph">
                  <h1 class="h1" style="margin-bottom: 10px;">{{$t('dashboard.graph')}}</h1>
                </div >
              </ods-col>
              <ods-col>
                <ods-form :model="graphForm" ref="graphForm" :rules="graphFormRules">
                  <div class="header-graph" >
                    <ods-form-item prop="initDate" >
                      <ods-date-picker class="select-width-date"
                        v-model="graphForm.initDate"
                        type="date"
                        :placeholder="$t('dashboard.initDate')"
                        format="yyyy/MM/dd"
                        value-format="timestamp">
                      </ods-date-picker>
                    </ods-form-item>
                    <ods-form-item>
                      <h4 class="h4" style="margin-right: 8px; margin-left: 8px;">TO</h4>
                    </ods-form-item>
                    <ods-form-item prop="endDate" >
                      <ods-date-picker class="select-width-date"
                      v-model="graphForm.endDate"
                      type="date"
                      :placeholder="$t('dashboard.endDate')"
                      format="yyyy/MM/dd"
                      value-format="timestamp">
                      </ods-date-picker>
                    </ods-form-item>
                    <ods-form-item style="margin-right: 8px; margin-left: 8px" prop="argument" >
                      <ods-select class="select-width" v-model="graphForm.argument" :placeholder="$t('dashboard.argument')" v-on:change="checkIfDisable(graphForm.argument)">
                        <ods-option
                          v-for="item in argument"
                          :key="item.value"
                          :label="item.label"
                          :value="item.value">
                        </ods-option>
                      </ods-select>
                    </ods-form-item>
                    <ods-form-item style="margin-right: 8px; margin-left: 16px" prop="groupBy" >
                      <ods-select class="select-width" v-model="graphForm.groupBy" :placeholder="$t('dashboard.groupBy')">
                        <ods-option
                          v-for="item in groupBy"
                          :key="item.value"
                          :label="item.label"
                          :value="item.value"
                          :disabled="disabledGroupBy">
                        </ods-option>
                      </ods-select>
                    </ods-form-item>
                    <ods-form-item style="margin-right: 8px; margin-left: 16px" prop="device">
                      <ods-select class="select-width" v-model="graphForm.device" :placeholder="$t('dashboard.device')" v-on:change="getClusters(graphForm.device)">
                        <ods-option
                          v-for="macdevice in optionDevices"
                          :key="macdevice"
                          :label="macdevice"
                          :value="macdevice">
                        </ods-option>
                      </ods-select>
                    </ods-form-item>
                    <ods-form-item style="margin-right: 8px; margin-left: 8px" prop="clusters" >
                      <ods-select class="select-width" v-model="graphForm.cluster" :placeholder="$t('dashboard.cluster')" v-on:change="getAttributes(graphForm.device,graphForm.cluster)">
                        <ods-option
                          v-for="cluster in optionClusters"
                          :key="cluster"
                          :label="cluster"
                          :value="cluster">
                        </ods-option>
                      </ods-select>
                    </ods-form-item>
                    <ods-form-item style="margin-right: 8px; margin-left: 8px" prop="Attributes" >
                      <ods-select class="select-width" v-model="graphForm.attribute" :placeholder="$t('dashboard.attribute')">
                        <ods-option
                          v-for="attribute in optionAttributes"
                          :key="attribute"
                          :label="attribute"
                          :value="attribute">
                        </ods-option>
                      </ods-select>
                    </ods-form-item>
                    <ods-form-item>
                      <ods-button size="small" @click="getResultGraph('graphForm')">{{$t('button.confirm')}}</ods-button>
                    </ods-form-item>
                  </div>
                </ods-form>
              </ods-col>
            </ods-row>
          </ods-row>
          <ods-row v-if="graph === null" type="flex" justify="center" >
            <ods-row class="box">
              <h3 class="h4">{{$t('dashboard.noData')}}</h3>
              <h4 class="h5">{{$t('dashboard.dataGraph')}}</h4>
            </ods-row>
          </ods-row>
          <ods-row v-else-if="graph !== null  && graph.series.length === 0" type="flex" justify="center" >
            <ods-row class="box">
              <h3 class="h4">{{$t('dashboard.noData')}}</h3>
              <h4 class="h5">{{$t('dashboard.noDataGraph')}}</h4>
            </ods-row>
          </ods-row>
          <ods-row v-else-if="graph !== null && graph.series.length !== 0">
            <ods-chart type="singleBar" :data="graph" orientation="vertical" :average="true" symbol=""></ods-chart>
          </ods-row>
        </ods-module>
      </ods-col>
    </ods-row>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'DashBoard',
  data () {
    return {
      graph: {
        labels: [],
        series: []
      },
      loading: false,
      optionDevices: [],
      devices: [],
      groupBy: [{
        value: '1h',
        label: 'Horario'
      }, {
        value: '1d',
        label: 'Diario'
      }, {
        value: '1w',
        label: 'Weekly'
      }],
      argument: [{
        value: 'mean',
        label: this.$t('argument.mean')
      }, {
        value: 'absolute',
        label: this.$t('argument.absolute')
      }, {
        value: 'count',
        label: this.$t('argument.count')
      }],
      alerts: null,
      disabledGroupBy: false,
      optionClusters: [],
      optionAttributes: [],
      graphForm: {
        initDate: '',
        endDate: '',
        device: '',
        cluster: '',
        argument: '',
        groupBy: '',
        attribute: ''
      },
      graphFormRules: {
        initDate: [
          { required: true, message: 'Introduzca una fecha de inicio', trigger: 'blur' }
        ],
        endDate: [
          { required: true, message: 'Introduzca una fecha de fin', trigger: 'blur' }
        ],
        argument: [
          { required: true, message: 'Introduzca un argumento', trigger: 'blur' }
        ],
        groupBy: [
          { required: true, message: 'Introduzca una agrupacin', trigger: 'blur' }
        ],
        deviceId: [
          { required: true, message: 'Introduzca un dipsositivo', trigger: 'blur' }
        ],
        signalId: [
          { required: true, message: 'Introduzca una seal', trigger: 'blur' }
        ]
      },
      porcentaje: null,
      nDevicesKO: null,
      nDevices: null,
      nSignalsReportables: null,
      nAttKO: null,
      nAttOK: null
    }
  },
  created () {
    this.getDevicesStatus()
    this.getDeviceGraph()
    this.getAlerts()
    this.interval = setInterval(function () {
      this.getDevicesStatus()
    }.bind(this), 20000)
  },
  methods: {
    getResultGraph (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.loading = true
          this.graph = null
          axios.put(this.$url + '/api/graph', this.graphForm).then(response => {
            this.graph = response.data
            if (this.graph === null) {
              this.$notify({
                title: 'Warning',
                message: this.$t('messagesBox.noData'),
                type: 'warning'
              })
            } else if (this.graph === 'NODB') {
              this.$notify({
                title: 'Error',
                message: this.$t('messagesBox.noDatabase'),
                type: 'error'
              })
            } else {
              this.$notify({
                title: 'Success',
                message: this.$t('messagesBox.graph'),
                type: 'success'
              })
            }
            this.loading = false
          })
        } else {
          this.$notify({
            title: 'Warning',
            message: this.$t('messagesBox.incorrectForm'),
            type: 'warning'
          })
          this.loading = false
          return false
        }
      })
    },
    getAlerts () {
      axios.get(this.$url + '/api/zbgetalerts').then(response => {
        // console.log(JSON.stringify(response.data))
        this.alerts = response.data
      })
    },
    getDeviceGraph () {
      this.optionDevices = []
      this.devices = []
      this.optionClusters = []
      this.optionAttributes = []
      axios.get(this.$url + '/api/zbdevicesgraph').then(response => {
        this.devices = response.data.data
        var i
        for (i = 0; i < this.devices.length; i++) {
          this.optionDevices.push(this.devices[i].mac)
        }
      }).catch(error => {
        console.log(error)
      })
    },
    getClusters (mac) {
      var i
      var j
      this.optionClusters = []
      this.optionAttributes = []
      for (i = 0; i < this.devices.length; i++) {
        if (this.devices[i].mac.localeCompare(mac) === 0) {
          for (j = 0; j < this.devices[i].clusters.length; j++) {
            this.optionClusters[j] = this.devices[i].clusters[j].name
          }
        }
      }
    },
    checkIfDisable (argument) {
      if (argument.localeCompare('absolute') === 0) {
        this.disabledGroupBy = true
        this.graphForm.groupBy = 'N/A'
      } else {
        this.disabledGroupBy = false
        this.graphForm.groupBy = ''
      }
    },
    getAttributes (mac, clustername) {
      var i
      var j
      var m = 0
      for (i = 0; i < this.devices.length; i++) {
        if (this.devices[i].mac.localeCompare(mac) === 0) {
          for (j = 0; j < this.devices[i].clusters.length; j++) {
            if (this.devices[i].clusters[j].name.localeCompare(clustername) === 0) {
              for (m = 0; m < this.devices[i].clusters[j].attributes.length; m++) {
                this.optionAttributes[m] = this.devices[i].clusters[j].attributes[m]
              }
            }
          }
        }
      }
    },

    getDevicesStatus () {
      this.porcentaje = null
      axios.get(this.$url + '/api/zbattstatus').then(response => {
        this.porcentaje = response.data.gauge
        console.log(JSON.stringify(response.data.gauge))
        this.nDevices = response.data.ndevices
        this.nDevicesKO = response.data.nDevicesKO
        this.nSignalsReportables = response.data.nSignalsReportables
        this.nAttOK = response.data.nAttOK
        this.getAlerts()
      }).catch(error => {
        console.log(error)
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.container {
  padding: 5%;
}
.clearfix:before, .clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}
.card {
  margin-bottom: 5%;
}
.punto-rojo {
  border-radius: 6px;
  height: 9px;
  width: 9px;
  margin-top: 4px;
  background-color: #e0e0e6;
}
.gauge-row {
  display: flex;
  align-items: center;
  margin-left: auto;
  width: 250px;
  margin-right: auto;
}
.fixed-card {
  overflow: hidden;
  position: relative;
  height: 450px;
  margin-bottom: 40px;
}
.gauge-col {
  display: flex;
  align-items: end;
  width: 250px;
}
.punto-verde {
  border-radius: 6px;
  height: 10px;
  width: 10px;
  margin-top: 3px;
  background-color: #87BEE6;
}
.punto_verde {
  width: 8px;
  height: 8px;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  border-radius: 50%;
  background: #79C5B4;
  margin-top: 3px;
}
.select-margin {
   margin-right: 16px;
}
.structure-header {
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
}
.table-style {
  font-size: 12px;
  line-height: 1px;
}
.punto_rojo {
  width: 8px;
  height: 8px;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  border-radius: 50%;
  background: #DE2F2F;
  margin-top: 3px;
}
.border-bottom{
  border-bottom: solid;
  border-color: rgb(170, 170, 170);
  border-width: 1px;
}
.header-graph {
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  flex-direction: row;
  flex-wrap: nowrap;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  align-items: center;
  position: relative;
  width: auto;
  height: 100%;
  border-bottom: 0;
}
.padding-top-bottom{
  padding-top: 11px;
  padding-bottom: 11px;
}
.h1 {
  color: #060e14;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
}
.h2 {
  font-size: 10px;
  margin-bottom: 10px;
  font-weight: 500;
  color: #060E14;
}
.h3 {
  color: grey;
  font-size: 13px;
}
.h4 {
  color: grey;
  font-size: 9px;
  text-transform: uppercase;
  margin-bottom: 5px;
}
.h5 {
  color: #060E14;
  font-size: 14px;
}
.legend {
  color: grey;
  font-size: 9px;
  text-transform: uppercase;
  margin-bottom: 10px;
}
.graph-card{
  height: 460px;
  margin-bottom: 40px;
  margin-top: 40px;
  padding-top: 0px;
}
.green {
  color: #79C5B4;
}
.red {
  color: #DE2F2F
}
.dev-signals {
  height: 20px;
  width: 89px;
  color: #060E14;
  font-size: 12px;
  font-weight: 500;
  line-height: 20px;
  text-align: center;
  }
.number {
  font-size: 35px;
  font-weight: 500;
  margin-bottom: 10px;
}
.label-numbers {
  height: 16px;
  width: 130px;
  color: #6E767D;
  font-size: 11px;
  font-weight: 500;
  line-height: 16px;
  margin-right: 50px
  }
.rectangle-volumen {
  height: 464px;
  width: 579px;
  border-radius: 4px;
  background-color: #FFFFFF;
  box-shadow: 0 2px 10px 0 rgba(0,0,0,0.05);
  }
.border {
  padding-bottom: 20px;
  border-bottom: solid;
  border-width: 1px;
  border-color:#D8D8D8;
}
.devices-with-err {
  height: 16px;
  width: 134px;
  color: #6E767D;
  font-size: 9px;
  font-weight: 500;
  line-height: 16px;
  }
.select-width-date {
  margin-right: 16px;
  max-width: 140px;
}
.INFO {
  margin-left: 10px;
  width: 10px;
  height: 10px;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  border-radius: 50%;
  background: #79C5B4;
  cursor: pointer;
}
.WARNING {
  margin-left: 10px;
  width: 10px;
  height: 10px;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  border-radius: 50%;
  background: rgb(200, 231, 21);
  cursor: pointer;
}
.ERROR {
  margin-left: 10px;
  width: 10px;
  height: 10px;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  border-radius: 50%;
  background: #DE2F2F;
  cursor: pointer;
}
.linea {
  margin-top: 8px;
  height: 1px;
  width: 213px;
  background-color: #D5DCE0
  }
</style>



