<template>
  <div>
    <ods-row>
      <coor-card></coor-card>
    </ods-row>
    <ods-row :gutter="16" class="containerCards">
      <ods-col :xs="24" :sm="12" :md="12" :lg="8" :xl="6" v-for="device in devicesList" :key="device.mac">
        <device-card v-bind:device="device" v-on:refreshDevices='getDevices'></device-card>
      </ods-col>
    </ods-row>
  </div>
</template>

<script>

import axios from 'axios'
import DeviceCard from '@/components/DeviceCard'
import CoorCard from '@/components/CoorCard'
export default {
  name: 'Devices',
  components: {
    DeviceCard,
    CoorCard
  },
  data () {
    return {
      devicesList: []
    }
  },
  created () {
    this.getDevices()
    this.interval = setInterval(function () {
      this.getDevices()
    }.bind(this), 5000)
  },
  methods: {
    getDevices () {
      axios.get(this.$url + '/api/zbdevices').then(response => {
      // JSON responses are automatically parsed.
        this.devicesList = response.data.data
        // console.log(JSON.stringify(this.devicesList))
      }).catch(error => {
        console.log(error)
      // console.log(response.data)
      })
    },
    beforeDestroy: function () {
      clearInterval(this.interval)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.containerCards {
  padding: 16px;
}
</style>



