<template>
  <div>
    <ods-row type="flex" class="top-header">
      <ods-col class="button-left" :span="24">
        <ods-button class="h3" icon="ods-icon-arrow-left" type="text" size="mini" @click="$router.go(-1)">{{$t('button.back')}}</ods-button>
      </ods-col>
      <ods-col :span="24">
        <h1 class="h1">{{$t('dialog.confreport')}}</h1>
      </ods-col>
    </ods-row>
    <ods-row class="container">
      <ods-row :gutter="10">
        <ods-col :span="10">
          <ods-select style="width: 80%; margin-top: 15px; margin-left: 25px" v-model="cluster" placeholder="Clusters" v-on:change="getatts(cluster)">
            <ods-option
              v-for="item in options"
              :key="item"
              :label="item"
              :value="item">
            </ods-option>
          </ods-select>
        </ods-col>
        <ods-col :span="10">
          <ods-select style="width: 72%;margin-top: 15px; margin-left: 65px" v-model="attribute" placeholder="Attributes">
            <ods-option
              v-for="item in optionsatt"
              :key="item"
              :label="item"
              :value="item">
            </ods-option>
          </ods-select>
        </ods-col>
      </ods-row>
      <ods-row :gutter="10">
        <ods-col :span="10">
          <ods-input type="number" style="width: 80%; margin-top: 45px; margin-left: 25px" placeholder="Min time (s)" v-model="mintime"></ods-input>
        </ods-col>
        <ods-col :span="9">
          <ods-input type="number" style="width: 80%; margin-top: 45px; margin-left: 65px" placeholder="Max time (s)" v-model="maxtime"></ods-input>
        </ods-col>
      </ods-row>
      <ods-row>
        <div style="margin-top: 20px">
            <ods-checkbox style="margin-top: 15px; margin-left: 25px" v-model="checked">{{$t('dialog.automaticconf')}}</ods-checkbox>
        </div>
      </ods-row>
    </ods-row>
    <ods-row class="top-header" type="flex" justify="end">
      <ods-col class="button-right">
        <ods-button  size="medium" @click="configreport()">{{$t('button.submit')}}</ods-button>
      </ods-col>
    </ods-row>
  </div>
</template>

<script>

import axios from 'axios'

export default {
  name: 'updateDevice',
  data () {
    return {
      deviceForm: {},
      checked: false,
      labels: {
        buttonback: this.$t('button.back'),
        buttonconfirm: this.$t('button.confirm')
      },
      options: [],
      optionsatt: [],
      attribute: '',
      cluster: '',
      mintime: '',
      maxtime: ''
    }
  },
  created () {
    axios.get(this.$url + '/api/zbclusters', {params: {mac: this.$route.params.mac, onlyreportables: 'true'}}).then(response => {
      this.options = response.data.data
    })
  },
  methods: {
    getatts (zcl) {
      axios.get(this.$url + '/api/zbattributes', {params: {mac: this.$route.params.mac, cluster: zcl}}).then(response => {
        this.optionsatt = response.data.data
      })
        .catch(error => {
          console.log(error)
        })
    },
    showsuccessmsg () {
      this.$notify({
        title: 'Success',
        message: this.$t('successmsgs.confreport'),
        type: 'success'
      })
    },
    showerrormsg () {
      this.$notify({
        title: 'Error',
        message: this.$t('errormsgs.configreport'),
        type: 'error'
      })
    },
    configreport () {
      if (this.checked) {
        axios.put(this.$url + '/api/zbconfigreport?mac=' + this.$route.params.mac).then(response => {
          this.optionsatt = response.data.data
          this.showsuccessmsg()
        })
          .catch(error => {
            this.showerrormsg()
            console.log(error)
          })
      } else {
        axios.put(this.$url + '/api/zbconfigreport?mac=' + this.$route.params.mac + '&cluster=' + this.cluster + '&attribute=' + this.attribute + '&mintime=' + this.mintime + '&maxtime=' + this.maxtime).then(response => {
          this.optionsatt = response.data.data
          this.showsuccessmsg()
        }).catch(error => {
          console.log(error)
          this.showerrormsg()
        })
          .catch(error => {
            console.log(error)
            this.showerrormsg()
          })
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.button-left {
  margin-bottom: 40px;
  margin-top: 40px;
  margin-left: -12px;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  flex-direction: row;
  flex-wrap: nowrap;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  align-items: center;
  position: relative;
  width: auto;
  height: 100%;
}
.container2 {
  width: 80%;
  margin-top: 30px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 10px;
  border-color: rgba(88, 138, 173, 0.377);
  border-width: 2px;
  border-radius: 15px;
  background-color: white;
  padding: 2%;
}
.button-right {
  margin-bottom: 20px;
  margin-top: 20px;
  width: auto
}
.container {
  width: 55%;
  margin-top: 2%;
  margin-left: auto;
  margin-right: auto;
  background-color: white;
  padding: 2%;
  box-shadow: 0px 0px 5px 1px rgb(218, 218, 218);
}
.top-header {
  display: flex;
  align-items: center;
  width: 55%;
  margin-left: auto;
  margin-right: auto;
}
.h1 {
  font-size: 22px;
  margin-bottom: 30px;
}
.h3 {
  color: #2E6C99;
  font-size: 10px;
  text-transform: uppercase;
  margin-top: -5px;
}
</style>



