<template>
  <div class="container">
    <h1>{{$t('config.langModify')}}</h1>
    <br/>
    <ods-select v-model="$i18n.locale" placeholder="$t('config.langModify')">
      <ods-option
        v-for="item in langs"
        :key="item.value"
        :label="item.label"
        :value="item.value">
      </ods-option>
    </ods-select>
     <div style="text-align: center; margin-top: 15px;">
      <ods-button type="primary" @click="$router.go(-1)">{{$t('button.apply')}}</ods-button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Config',
  data () {
    return {
      langs: [
        {
          label: 'Espaol',
          value: 'es'
        },
        {
          label: 'English',
          value: 'en'
        }]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.container {
  width: 55%;
  margin-top: 10%;
  margin-left: auto;
  margin-right: auto;
  background-color: white;
  padding: 2%;
  box-shadow: 0px 0px 5px 1px rgb(218, 218, 218);
}
</style>



