<template lang="pug">
  ods-form.login-form__form(
    :model="loginForm"
    :rules="rules"
    ref="loginForm")
    div
      h1.login-form__title {{ $t('login.title') }}
      .login-form__errors.ods-mb-5
        ods-alert(
          v-show="errors"
          :title="errorInfo || $t('login.loginError')"
          type="error"
          :closable="false")
      ods-form-item(:label="$t('login.user')" prop='user')
        ods-input(
          :class="{error: errors}"
          type="text"
          v-model="loginForm.user")
      ods-form-item.ods-mb-3(:label="$t('login.password')" prop="password")
        ods-input(
          :class="{error: errors}"
          type="password"
          v-model="loginForm.password")
    div.login-form__actions
      ods-checkbox(v-model="keepMeLogged") {{ $t('login.keepMeLogged') }}
      ods-button(type="primary" native-type="submit" @click.prevent="submitForm('loginForm')") {{ $t('login.login') }}
</template>

<script>
import FormStyles from '@/components/login/LoginFormStyles.vue'
import { mapActions } from 'vuex'
export default {
  name: 'LoginForm',
  mixins: [ FormStyles ],
  data () {
    return {
      loginForm: {
        user: '',
        password: ''
      },
      rules: {
        user: [
          { required: true, message: this.$t('login.rules.user'), trigger: 'submit' }
        ],
        password: [
          { required: true, message: this.$t('login.rules.password'), trigger: 'submit' }
        ]
      },
      keepMeLogged: true,
      errors: false,
      errorInfo: ''
    }
  },
  methods: {
    ...mapActions([
      'login',
      'loader'
    ]),
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.loginForm.user === 'admin' && this.loginForm.password === 'admin') {
            sessionStorage.sessionToken = 'token'
            sessionStorage.userId = 'administrator'
            this.$notify.closeAll()
            this.$router.push('/')
          } else {
            this.$notify({
              title: 'Error',
              message: this.$t('login.loginError'),
              type: 'error',
              position: 'top-right',
              duration: 5000
            })
          }
        } else {
          this.$notify({
            title: 'Error',
            message: this.$t('login.formNotValid'),
            type: 'error',
            position: 'top-right',
            duration: 5000
          })
        }
        return false
      })
    }
  }
}

</script>



