<template>
    <ods-menu
        class="ods-menu-vertical-demo"
        @open="handleOpen"
        @close="handleClose"
        :collapse="isCollapse"
        menuTrigger="click"
        position="fixed"
        :isFirstLevel="true">
        <li class="ods-menu--logo" @click="isCollapse = !isCollapse">
            <ods-icon class="ods-menu--logo__icon" name="logo"></ods-icon>
        </li>
        <ods-menu-item index="1" v-on:click="toDashboard">
            <i class="ods-icon-dashboard"></i>
            <span slot="title">{{$t('menu.dashboard')}}</span>
        </ods-menu-item>
        <ods-menu-item index="2" v-on:click="toDevices">
           <i class="ods-icon-broadcast"></i>
           <span slot="title" >{{$t('menu.devices')}}</span>
        </ods-menu-item>
        <!-- <ods-menu-item index="3" @click="change('1880')">
            <img src="static/ico_nodered.svg"/>
            <span slot="title" >{{$t('menu.nodered')}}</span>
        </ods-menu-item>
        <ods-menu-item index="4" @click="change('4200')">
            <img src="static/ico_terminal.svg"/>
            <span slot="title" >{{$t('menu.shell')}}</span>
        </ods-menu-item> -->
    </ods-menu>
</template>

<script>
export default {
  name: 'sideNavigation',
  data () {
    return {
      isCollapse: true
    }
  },
  methods: {
    handleClose () {
      console.log('close')
    },
    handleOpen () {
      console.log('open')
    },
    toDevices () {
      this.$router.push('/zigbee/devices')
    },
    toDashboard () {
      this.$router.push('/zigbee/dashBoard')
    }
    // change (port) {
    //   let app = window.open('https://' + window.location.hostname + ':' + port)
    //   app.focus()
    // }
  }
}
</script>

<style lang="scss" scoped>
</style>



