<template>
  <div class="structure-header">
    <div class="header">
      <h1>Node1 - Zigbee</h1>
    </div>
    <div class="header-tools">
      <ods-tooltip effect="dark" :content="$t('button.setting')" placement="bottom">
        <ods-button class="padding" @click="toConfig" icon="ods-icon-setting" type="text" size="small"></ods-button>
      </ods-tooltip>
      <ods-tooltip effect="dark" :content="$t('button.help')" placement="bottom">
        <ods-button class="padding" icon="ods-icon-help" type="text" size="small"></ods-button>
      </ods-tooltip>
      <ods-tooltip effect="dark" :content="$t('button.notification')" placement="bottom">
        <ods-button class="padding" icon="ods-icon-notification" type="text" size="small"></ods-button>
      </ods-tooltip>
      <ods-avatar class="img-logo" size="large">
        <img  src="static/zigbee-icon.svg">
      </ods-avatar>
  </div>
</div>
</template>

<script>
export default {
  name: 'TheHeader',
  data () {
    return {
    }
  },
  methods: {
    toConfig () {
      this.$router.push('/zigbee/config')
    }
  }
}
</script>

<style lang="scss" scoped>
.structure-header {
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;

  .header {
    order: 0;
    padding: 0 24px;
    display: flex;
    align-items: center;
    height: 64px;
  }

  .header-tools {
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    flex-direction: row;
    flex-wrap: nowrap;
    -webkit-box-pack: end;
    -ms-flex-pack: end;
    justify-content: flex-end;
    align-items: center;
    position: relative;
    width: auto;
    height: 100%;
    border-bottom: 0;
    height: 72px;
  }
  .padding {
    padding: 0px 0px 0px 0px;
    margin-bottom: 0px;
  }
  .img-logo {
    margin-left: 28px;
    margin-top: 25px;
    width: 40px;
  }

  .client-logo {
    position: relative;
    width: 160px;
    height: 100%;
    text-align: center;
    border-left: 1px solid #d9d9d9;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;

    svg {
      position: relative;
      margin: auto;
      width: 100px;
    }
  }
}

@media only screen and ($--breakpoint-menu) {
  .structure-header {
    flex-wrap: wrap;

    .header {
      order: 2;
    }

    .header-tools {
      width: 100%;
      border-bottom: 1px solid #d9d9d9;
    }
  }
}
</style>



