import Vue from 'vue'
import Router from 'vue-router'
import DashBoard from '@/views/DashBoard'
import Devices from '@/views/Devices'
import Config from '@/views/Config'
import DevInfo from '@/views/DevInfo'
import ConfReport from '@/views/ConfReport'
import Login from '@/views/login/Login'
import LoginForm from '@/components/login/LoginForm'
import LoginPasswordForm from '@/components/login/LoginPasswordForm'

Vue.use(Router)

var context = '/zigbee'

const route = new Router({
  routes: [
    {
      path: context + '/login',
      name: 'Login',
      component: Login,
      redirect: { name: 'login-form' },
      children: [
        {
          path: '',
          name: 'login-form',
          component: LoginForm
        },
        {
          path: 'password',
          name: 'password-form',
          component: LoginPasswordForm
        }
      ]
    },
    {
      path: context + '/dashBoard',
      name: 'DashBoard',
      component: DashBoard,
      meta: {
        private: true
      }
    },
    {
      path: context + '/devices',
      name: 'devices',
      component: Devices,
      meta: {
        private: true
      }
    },
    {
      path: context + '/config',
      name: 'config',
      component: Config,
      meta: {
        private: true
      }
    },
    {
      path: context + '/devices/:mac/confreport',
      name: 'confReport',
      component: ConfReport,
      meta: {
        private: true
      }
    },
    {
      path: context + '/devices/:mac/devinfo',
      name: 'devInfo',
      component: DevInfo,
      meta: {
        private: true
      }
    },
    {
      path: '/*',
      redirect: { name: 'DashBoard' },
      meta: {
        private: true
      }
    }
  ]
})

route.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.private)) {
    console.log('TOKEN: ' + sessionStorage.getItem('sessionToken'))
    if (!sessionStorage.getItem('sessionToken')) {
      next({name: 'Login'})
    } else {
      next()
    }
  } else {
    next()
  }
})

export default route



