<template>
  <ods-row>
    <ods-module class="containerCoor">
      <ods-row :gutter="3" style="margin-top: 20px;">
        <ods-col :xs="4" :sm="4" :md="4" :lg="4" :xl="4" class="border-right">
          <img style="background-color: white; margin-left: 20%" width="50px" src='static/node1_intel_v1.svg'>
        </ods-col>
        <ods-col :xs="5" :sm="5" :md="5" :lg="5" :xl="5" class="border-right">
          <ods-row class="center" >{{$t('zbcoord.mac')}}</ods-row>
          <ods-row class="center font-18 line-height" > {{coor.mac}} </ods-row>
        </ods-col>
        <ods-col :xs="5" :sm="5" :md="5" :lg="5" :xl="5" class="border-right">
          <ods-row class="center" >{{$t('zbcoord.fwversion')}}</ods-row>
          <ods-row class="center font-18 line-height" > {{coor.fwversion}} </ods-row>
        </ods-col>
        <ods-col :xs="5" :sm="5" :md="5" :lg="5" :xl="5" class="border-right">
          <ods-row class="center" >{{$t('zbcoord.channel')}}</ods-row>
          <ods-row class="center font-18 line-height" >{{coor.channel}}</ods-row>
        </ods-col>
        <ods-col :xs="5" :sm="5" :md="5" :lg="5" :xl="5" class="center middle" >
          <ods-button v-loading.fullscreen.lock="loading" @click="opennetwork()" plain>{{$t('zbcommands.opennetwork')}}</ods-button>
        </ods-col>
      </ods-row>
    </ods-module>
  </ods-row>
</template>

<script>
import axios from 'axios'

export default {
  name: 'DeviceCard',
  data () {
    return {
      coor: '',
      labels: {
        fwversion: this.$t('button.cancel'),
        update: this.$t('button.update'),
        delete: this.$t('button.delete'),
        signals: this.$t('button.signals'),
        dialog: this.$t('dialog.msg'),
        opennetwork: this.$t('zbcommands.opennetwork')
      },
      loading: false
    }
  },
  created () {
    axios.get(this.$url + '/api/zbcoorinfo').then(response => {
      this.coor = response.data.data
    })
  },
  methods: {
    opennetwork () {
      this.loading = true
      axios.get(this.$url + '/api/zbactivenet')
        .then(response => {
          this.loading = false
          this.$notify({
            title: 'Success',
            message: this.$t('successmsgs.network'),
            type: 'success'
          })
        })
        .catch(error => {
          this.$notify({
            title: 'Error',
            message: this.$t('errormsgs.network'),
            type: 'error'
          })
          this.loading = false
          console.log(error)
        })
    }
  }
}
</script>

<style lang="scss" scoped>
.border-right {
  border-right: solid;
  border-width: 1px;
  border-color: #2E6C99;
}
.containerCoor {
  height: 88px;
  background-color: #E3EBF1;
  color: #2E6C99;
  border-radius: 0px;
 }
 .center {
   text-align: center;
 }
 .font-18 {
   font-size: 18px;
   line-height: 25px;
 }
 .middle {
   margin-top: 5px;
 }
</style>



