<template>
  <div>
    <ods-module class="container">
      <ods-row class="border">
        <ods-row>
          <ods-col>
            <div class="menu-devtype">{{device.mac}}</div>
          </ods-col>
          <ods-col :offset="18">
            <ods-dropdown style="margin-left: 20px;" @command="handleCommand" trigger="click">
              <span class="ods-dropdown-link">
                <i class="ods-icon-configure"></i>
              </span>
              <ods-dropdown-menu slot="dropdown">
                <ods-dropdown-item v-if="checkIfIdentify(device) === true" command="identify"><ods-icon style="margin-right: 15px" name="location" size="16" color="#6E767D"/>{{$t('dialog.identify')}}</ods-dropdown-item>
                <ods-dropdown-item v-if="checkIfOnOff(device) === true" command="toggle"><ods-icon style="margin-right: 15px" name="configure" size="16" color="#6E767D"/>{{$t('dialog.toggle')}}</ods-dropdown-item>
                <ods-dropdown-item v-if="checkIfClusterThermostat(device) === true" command="confthermostat"><ods-icon style="margin-right: 15px" name="edit" size="16" color="#6E767D"/>{{$t('dialog.confthermostat')}}</ods-dropdown-item>
              </ods-dropdown-menu>

              <ods-dialog v-loading="loading" :title='labels.confthermostat' :visible.sync="thermostatdialogVisible" width="30%">
                <div style="margin-bottom: 5px"><span>{{$t('thermostat.setpoint')}}: {{thermostat.setpoint}}</span></div>
                <div style="margin-bottom: 5px"><span>{{$t('thermostat.mode')}}: {{thermostat.mode}}</span></div>
                <div style="margin-bottom: 15px"><span>{{$t('thermostat.localtemperature')}}: {{thermostat.localtemperature}}</span></div>
                <div style="margin-bottom: 5px">
                  <ods-select v-model="mode" :placeholder="thermostat.mode" v-on:change="changeMode = true">
                    <ods-option
                        v-for="item in optionsThermostat"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                    </ods-option>
                  </ods-select>
                </div>
                <ods-slider style="margin-bottom: 5px" v-model="temperatura" :format-tooltip="formatTooltip" :min="160" :max="320" v-on:change="changeSetpoint = true"></ods-slider>
                <ods-checkbox v-model="changeSetpoint">{{$t('dialog.changesetpoint')}}</ods-checkbox>
                  <ods-checkbox v-model="changeMode">{{$t('dialog.changemode')}}</ods-checkbox>
                <div style="text-align: center; margin-top: 10px;">
                  <ods-button type="primary" @click="setThermostat(device.mac,mode,temperatura)">{{$t('button.confirm')}}</ods-button>
                </div>
              </ods-dialog>
            </ods-dropdown>
            <ods-dropdown style="margin-left: 20px;" @command="handleCommand" trigger="click">
              <span class="ods-dropdown-link">
                <i class="ods-icon-overflow-menu"></i>
              </span>
              <ods-dropdown-menu slot="dropdown">
                <ods-dropdown-item command="delete"><ods-icon style="margin-right: 15px" name="delete" size="16" color="#6E767D"/>{{labels.delete}}</ods-dropdown-item>
                <ods-dropdown-item command="confreport"><ods-icon style="margin-right: 15px" name="equivalent" size="16" color="#6E767D"/>{{$t('dialog.conf')}}</ods-dropdown-item>
                <ods-dropdown-item command="devinfo"><img class="margin-right-15 margin-img" src="static/icon_senales.svg" color="#6E767D"/>{{labels.devinfo}}</ods-dropdown-item>
                <ods-dropdown-item v-if="checkIfClusterOTA(device) === true" command="upgrade"><ods-icon style="margin-right: 15px" name="upload" size="16" color="#6E767D"/>{{labels.upgrade}}</ods-dropdown-item>
                <ods-dropdown-item v-if="device.status === 'DOWNLOADING'" command="abort"><ods-icon style="margin-right: 15px" name="error" size="16" color="#6E767D"/>{{labels.abortOTA}}</ods-dropdown-item>
                <ods-dropdown-item v-loading="loading" v-if="checkIfClusterOTA(device) === true  && (device.status === 'OK' || device.status === 'KO')" command="version"><ods-icon style="margin-right: 15px" name="info" size="16" color="#6E767D"/>{{labels.versionDevice}}</ods-dropdown-item>
              </ods-dropdown-menu>
              <ods-dialog v-loading="loading" :title='labels.deviceOTAVersion' :visible.sync="deviceOTAVersiondialogVisible" width="30%">
                <div style="margin-bottom: 15px"><span>{{$t('deviceVersion.currentFileVersion')}}: {{deviceVersion.currentFileVersion}}</span></div>
                <div style="margin-bottom: 15px"><span>{{$t('deviceVersion.stackBuild')}}: {{deviceVersion.stackBuild}}</span></div>
                <div style="margin-bottom: 15px"><span>{{$t('deviceVersion.stackRelease')}}: {{deviceVersion.stackRelease}}</span></div>
                <div style="margin-bottom: 15px"><span>{{$t('deviceVersion.applicationBuild')}}: {{deviceVersion.applicationBuild}}</span></div>
                <div style="margin-bottom: 15px"><span>{{$t('deviceVersion.applicationRelease')}}: {{deviceVersion.applicationRelease}}</span></div>
                <div style="text-align: center; margin-top: 15px;">
                  <ods-button type="primary" @click="getDeviceVersion(device)">{{$t('button.check')}}</ods-button>
                </div>
              </ods-dialog>
              <ods-dialog :title="labels.removedevice" :visible.sync="deletedialogVisible" width="30%">
                <span>{{$t('dialog.msg')}}</span>
                <span slot="footer" class="dialog-footer">
                  <ods-button  @click="deletedialogVisible = false" plain>{{$t('button.cancel')}}</ods-button>
                  <!-- <ods-button v-on:click="deleteDevice(device.mac)">{{$t('button.confirm')}}</ods-button> -->
                  <ods-col :xs="5" :sm="5" :md="5" :lg="5" :xl="5" class="center middle" >
                    <ods-button v-loading.fullscreen.lock="loading" @click="deleteDevice(device.mac)" plain>{{$t('button.confirm')}}</ods-button>
                  </ods-col>
                </span>
              </ods-dialog>
              <ods-dialog :visible.sync="upgradedialogVisible" :show-close="false"
                :title="labels.upgradeConsiderations"
                icon="warning"
                width="30%"
                :append-to-body="true">
                <ods-row>
                  - {{labels.filepath}}
                </ods-row>
                <ods-row>
                  - {{labels.filename}}
                </ods-row>
                <ods-row>
                  - {{labels.absolutepath}}
                </ods-row>
                <ods-row>
                  - {{labels.compatiblesfwversions}}
                </ods-row>
                <span style="text-align: center" slot="footer" class="dialog-footer">
                  <ods-button  @click="upgradedialogVisible = false" plain>{{$t('button.cancel')}}</ods-button>
                  <ods-col :xs="5" :sm="5" :md="5" :lg="5" :xl="5" class="center middle" >
                    <ods-button v-loading.fullscreen.lock="loading" @click="upgradeDevice(device)" plain>{{$t('button.confirm')}}</ods-button>
                  </ods-col>
                </span>
              </ods-dialog>
              <ods-dialog
                title="Warning"
                :visible.sync="abortOTADownloadDialogConfirmation" :show-close="false"
                width="30%"
                :append-to-body="true"
                center>
                <span>{{$t('dialog.abortOTADownloadConfirmation')}}</span>
                <span slot="footer" class="dialog-footer">
                  <ods-button type="text" @click="abortOTADownloadDialogConfirmation = false">{{$t('button.cancel')}}</ods-button>
                  <ods-button type="primary" v-loading.fullscreen.lock="loading" @click="abortOTADownload(device)">{{$t('button.confirm')}}</ods-button>
                </span>
              </ods-dialog>
            </ods-dropdown>
          </ods-col>
        </ods-row>
      </ods-row>
      <ods-row class="padding" type="flex" justify="end">
        <img v-if="device.lq === 4" style="margin-left: 3px" src="static/signal_4.svg">
        <img v-if="device.lq === 3" style="margin-left: 3px" src="static/signal_3.svg">
        <img v-if="device.lq === 2" style="margin-left: 3px" src="static/signal_2.svg">
        <img v-if="device.lq === 1" style="margin-left: 3px" src="static/signal_1.svg">
        <img v-if="device.lq === 0 && device.status ==='OK'" src="static/signal_null.svg">
        <img v-if="device.lq === 0 && device.status ==='KO'" src="static/signal_off.svg">
      </ods-row>
      <ods-row :gutter="2">
        <ods-col :xs="12" :sm="12" :md="12" :lg="12" :xl="12" >
          <img width="118px" :src="getName (device)">
        </ods-col>
        <ods-col :xs="12" :sm="12" :md="12" :lg="12" :xl="12" >
          <div>
            <FONT class="watts">{{device.mainValues.mainValue}}</FONT>
          </div>
          <div style="margin-top: 5px">
            <ods-row>
              <ods-col class="divDeviceType">
                <h2 class="deviceType">{{device.devType}}</h2>
              </ods-col>
            </ods-row>
          </div>
        </ods-col>
      </ods-row>
      <ods-row class="padding-top">
        <ods-col>
          <ods-col class="cardData" :offset="18">
            <div v-if="device.status === 'OK'" class="divActive">
              <FONT class="onError">{{$t('zbdevice.statusOK')}}</FONT>
            </div>
            <div v-if="device.status === 'KO'" class="divError">
              <FONT class="onError">{{$t('zbdevice.statusKO')}}</FONT>
            </div>
            <div v-if="device.status ==='UPGRADING'" class="divUpgrading">
              <FONT class="onError">{{$t('zbdevice.statusUPGRADING')}}</FONT>
            </div>
            <div v-if="device.status ==='DOWNLOADING'" class="divUpgrading">
              <FONT class="onError">{{$t('dialog.downloadingOTAFile')}} {{device.percentage}}%</FONT>
              <!-- <FONT class="onError">{{$t('dialog.downloadingOTAFile')}} 20%</FONT> -->
            </div>
          </ods-col>
        </ods-col>
      </ods-row>
    </ods-module>
  </div>
</template>
<script>

import axios from 'axios'

export default {
  name: 'DeviceCard',
  props: ['device'],
  data () {
    return {
      temperatura: 30,
      thermostat: {
        mode: '',
        localtemperature: '',
        setpoint: ''
      },
      deviceVersion: {
        currentFileVersion: '',
        stackBuild: '',
        stackRelease: '',
        applicationBuild: '',
        applicationRelease: ''
      },
      mode: '',
      optionsThermostat: [{
        value: this.$t('thermostatmode.heat'),
        label: this.$t('thermostatmode.heat')
      }, {
        value: this.$t('thermostatmode.cool'),
        label: this.$t('thermostatmode.cool')
      }, {
        value: this.$t('thermostatmode.off'),
        label: this.$t('thermostatmode.off')
      }],
      labels: {
        update: this.$t('button.update'),
        delete: this.$t('button.delete'),
        signals: this.$t('button.signals'),
        buttoncancel: this.$t('button.cancel'),
        buttonconfirm: this.$t('button.confirm'),
        devinfo: this.$t('dialog.devsignals'),
        removedevice: this.$t('dialog.removedevice'),
        confthermostat: this.$t('thermostat.changeThermostat'),
        upgrade: this.$t('dialog.upgradeDevice'),
        upgradeConsiderations: this.$t('upgradeDialog.considerations'),
        filepath: this.$t('upgradeDialog.filepath'),
        filename: this.$t('upgradeDialog.filename'),
        absolutepath: this.$t('upgradeDialog.absolutepath'),
        compatiblesfwversions: this.$t('upgradeDialog.compatiblesfwversions'),
        unknownError: this.$t('errormsgs.unknownError'),
        abortOTA: this.$t('dialog.abortOTA'),
        versionDevice: this.$t('dialog.deviceVersion'),
        deviceOTAVersion: this.$t('dialog.deviceOTAVersion')
      },
      upgradeOption: {
        upgrade: 'upgrade',
        status: 'status',
        abort: 'abort'
      },
      upgradeFilePath: '/mnt/conf/file.zigbee',
      leaveCode: null,
      leaveDetails: null,
      deletedialogVisible: false,
      upgradedialogVisible: false,
      thermostatdialogVisible: false,
      interval: null,
      devmac: '',
      changeMode: false,
      changeSetpoint: false,
      abortOTADownloadDialogConfirmation: false,
      loading: false,
      deviceOTAVersiondialogVisible: false
    }
  },
  methods: {
    handleCommand (command) {
      if (command === 'devinfo') {
        this.toDevInfo(this.device)
      } else if (command === 'toggle') {
        this.toggleDevice(this.device.mac)
      } else if (command === 'delete') {
        this.deletedialogVisible = true
      } else if (command === 'identify') {
        this.identifyDevice(this.device.mac)
      } else if (command === 'confthermostat') {
        this.thermostatdialogVisible = true
        this.getThermostatInfo(this.device.mac)
      } else if (command === 'confreport') {
        this.toConfReport(this.device)
      } else if (command === 'upgrade') {
        this.upgradedialogVisible = true
      } else if (command === 'abort') {
        this.abortOTADownloadDialogConfirmation = true
      } else if (command === 'version') {
        this.deviceOTAVersiondialogVisible = true
      }
    },
    formatTooltip (val) {
      return (val / 10)
    },
    deleteDevice (devmac) {
      this.loading = true
      axios.delete(this.$url + '/api/zbremovedevice', {params: { mac: devmac }})
        .then(response => {
          this.leaveCode = response.data.leaveCode
          this.leaveDetails = response.data.leaveDetails
          if (this.leaveCode === 0) {
            this.$notify({
              title: 'Success',
              message: this.$t('successmsgs.removedevice'),
              type: 'success'
            })
          } else {
            this.$notify({
              title: 'Success',
              message: this.leaveDetails,
              type: 'warning'
            })
          }
        })
      this.deletedialogVisible = false
      this.loading = false
        .catch(error => {
          this.deletedialogVisible = false
          this.loading = false
          this.$notify({
            title: 'Success',
            message: this.$t('errormsgs.removedevice'),
            type: 'warning'
          })
          // window.location.reload()
          console.log(error)
        })
    },
    getDeviceVersion (device) {
      this.loading = true
      axios.get(this.$url + '/api/zbgetversion', {params: { mac: device.mac }})
        .then(response => {
          var status = response.data.status
          console.log(JSON.stringify(response.data))
          if (status.localeCompare('ERROR') === 0) {
            this.$notify.error({
              title: 'Error',
              duration: 5000,
              type: 'error',
              message: response.data.message
            })
          } else if (status.localeCompare('SUCCESS') === 0) {
            console.log('Exito')
            this.$notify({
              title: 'Success',
              duration: 5000,
              type: 'success'
            })
            this.deviceVersion.currentFileVersion = response.data.data.currentFileVersion
            this.deviceVersion.stackBuild = response.data.data.stackBuild
            this.deviceVersion.stackRelease = response.data.data.stackRelease
            this.deviceVersion.applicationBuild = response.data.data.applicationBuild
            this.deviceVersion.applicationRelease = response.data.data.applicationRelease
          }
          this.loading = false
        }).catch(error => {
          this.$notify.error({
            title: 'Error',
            duration: 5000,
            type: 'error',
            message: this.labels.unknownError
          })
          this.loading = false
          console.log(error)
        })
    },
    upgradeDevice (device) {
      this.loading = true
      axios.put(this.$url + '/api/zbotaupgrade?mac=' + device.mac + '&action=' + this.upgradeOption.upgrade + '&filepath=' + this.upgradeFilePath)
        .then(response => {
          var status = response.data.status
          // console.log(JSON.stringify(response.data))
          if (status.localeCompare('ERROR') === 0) {
            this.$notify.error({
              title: 'Error',
              duration: 5000,
              type: 'error',
              message: response.data.message
            })
          } else if (status.localeCompare('SUCCESS') === 0) {
            this.$notify({
              title: 'Success',
              duration: 5000,
              type: 'success',
              message: response.data.message
            })
          }
          this.loading = false
        })
        .catch(error => {
          this.$notify.error({
            title: 'Error',
            duration: 5000,
            type: 'error',
            message: this.labels.unknownError
          })
          this.loading = false
          console.log(error)
        })
    },
    abortOTADownload (device) {
      this.loading = true
      axios.put(this.$url + '/api/zbotaupgrade?mac=' + device.mac + '&action=' + this.upgradeOption.abort)
        .then(response => {
          var status = response.data.status
          console.log(JSON.stringify(response.data))
          if (status.localeCompare('ERROR') === 0) {
            this.$notify.error({
              title: 'Error',
              duration: 5000,
              type: 'error',
              message: response.data.message
            })
          } else if (status.localeCompare('SUCCESS')) {
            this.$notify({
              title: 'Success',
              duration: 5000,
              type: 'success',
              message: response.data.message
            })
          }
          this.loading = false
        })
        .catch(error => {
          this.$notify.error({
            title: 'Error',
            duration: 5000,
            type: 'error',
            message: this.labels.unknownError
          })
          this.loading = false
          console.log(error)
        })
    },
    click () {
      console.log('Notification has been clicked!')
    },
    close () {
      console.log('Notification has been closed!')
    },
    getThermostatInfo (devmac) {
      axios.get(this.$url + '/api/zbthermostatinfo', {params: { mac: devmac }})
        .then(response => {
          var termostato = response.data.data
          if (termostato.thermostatMode.localeCompare('Heat') === 0) {
            this.thermostat.setpoint = termostato.OHeatSetpoint
          } else if (termostato.thermostatMode.localeCompare('Cool') === 0) {
            this.thermostat.setpoint = termostato.OCoolSetpoint
          } else if (termostato.thermostatMode.localeCompare('Off') === 0) {
            this.thermostat.setpoint = ''
          }
          this.thermostat.mode = termostato.thermostatMode
          this.thermostat.localtemperature = termostato.localTemperature
        })
        .catch(error => {
          console.log(error)
        })
    },
    setThermostat (devmac, mode, temperature) {
      // cambiando el setpoint
      // http://localhost:8080/api/zbsetpoint?mac=000D6F000BF849E5&temperature=2800
      temperature = temperature * 10
      if ((this.changeSetpoint === true) && (this.changeMode === false)) {
        this.loading = true
        axios.put(this.$url + '/api/zbsetpoint?mac=' + devmac + '&temperature=' + temperature).then(response => {
          this.loading = false
          this.getThermostatInfo(devmac)
          this.$notify({
            title: 'Success',
            message: this.$t('successmsgs.thermostatsetpoint'),
            type: 'success'
          })
        })
          .catch(error => {
            this.loading = false
            this.$notify({
              title: 'Error',
              message: this.$t('errormsgs.thermostatsetpoint'),
              type: 'error'
            })
            console.log(error)
          })
      // http://localhost:8080/api/zbsetmode?mac=000D6F000BF849E5&mode=heat
      } else if ((this.changeSetpoint === false) && (this.changeMode === true)) {
        this.loading = true
        axios.put(this.$url + '/api/zbsetmode?mac=' + devmac + '&mode=' + mode).then(response => {
          this.loading = false
          this.getThermostatInfo(devmac)
          this.$notify({
            title: 'Success',
            message: this.$t('successmsgs.thermostatmode'),
            type: 'success'
          })
        })
          .catch(error => {
            this.loading = false
            this.$notify({
              title: 'Error',
              message: this.$t('errormsgs.thermostatmode'),
              type: 'error'
            })
            console.log(error)
          })
      } else if ((this.changeSetpoint === true) && (this.changeMode === true)) {
        this.loading = true
        axios.put(this.$url + '/api/zbsetmode?mac=' + devmac + '&mode=' + mode).then(response => {
          this.$notify({
            title: 'Success',
            message: this.$t('successmsgs.thermostatmode'),
            type: 'success'
          })
          axios.put(this.$url + '/api/zbsetpoint?mac=' + devmac + '&temperature=' + temperature).then(response => {
            this.loading = false
            this.getThermostatInfo(devmac)
            this.$notify({
              title: 'Success',
              message: this.$t('successmsgs.thermostatsetpoint'),
              type: 'success'
            })
          })
            .catch(error => {
              this.loading = false
              this.$notify({
                title: 'Error',
                message: this.$t('errormsgs.thermostatsetpoint'),
                type: 'error'
              })
              console.log(error)
            })
        })
          .catch(error => {
            this.loading = false
            this.$notify({
              title: 'Error',
              message: this.$t('errormsgs.thermostatmode'),
              type: 'error'
            })
            console.log(error)
          })
      }
    },
    toDevInfo (dev) {
      this.$router.push('/zigbee/devices/' + dev.mac + '/devinfo')
    },
    toConfReport (dev) {
      this.$router.push('/zigbee/devices/' + dev.mac + '/confreport')
    },
    getName: function (dev) {
      var devType = dev.devType
      if (((devType.localeCompare('METER')) === 0) || (devType.localeCompare('PLUG')) === 0) {
        return 'static/energy-plug.svg'
      } else if ((devType.localeCompare('THERMOSTAT')) === 0) {
        return 'static/hvac-thermostat.svg'
      } else if (((devType.localeCompare('CLAMP')) === 0) || (devType.localeCompare('CLAMP3')) === 0) {
        return 'static/energy-clamp.svg'
      } else if ((devType.localeCompare('SENSOR')) === 0) {
        return 'static/sensor-generic.svg'
      } else if ((devType.localeCompare('MOTION_SENSOR')) === 0) {
        return 'static/sensor-motion.svg'
      } else if ((devType.localeCompare('DOOR_SENSOR')) === 0) {
        return 'static/sensor-contact.svg'
      } else if ((devType.localeCompare('SMOKE_SENSOR')) === 0) {
        return 'static/sensor-smoke.svg'
      } else if ((devType.localeCompare('SWITCH_BUTTON')) === 0) {
        return 'static/energy-switch.svg'
      } else if ((devType.localeCompare('WATER_SENSOR')) === 0) {
        return 'static/sensor-water.svg'
      } else if ((devType.localeCompare('PANIC_SENSOR')) === 0) {
        return 'static/buttom-panic.svg'
      } else if ((devType.localeCompare('ALARM_SENSOR')) === 0) {
        return 'static/security-alarm.svg'
      } else if (((devType.localeCompare('BULB')) === 0) || (devType.localeCompare('BULB_COLOR')) === 0 || (devType.localeCompare('BULB_TEMP')) === 0) {
        return 'static/energy-bulb.svg'
      } else {
        return 'static/undefined.svg'
      }
    },
    toggleDevice (devmac) {
      axios.put(this.$url + '/api/zbtoggle?mac=' + devmac).then(response => {
      })
        .catch(error => {
          console.log(error)
        })
    },
    identifyDevice (devmac) {
      axios.put(this.$url + '/api/zbidentify?mac=' + devmac).then(response => {
      })
        .catch(error => {
          console.log(error)
        })
    },
    checkIfOnOff: function (item) {
      var onoff = false
      var clusters = item.clusters
      clusters.forEach(function (valor, indice, array) {
        if (valor.localeCompare('On/Off') === 0) {
          onoff = true
          return onoff
        }
      })
      return onoff
    },
    checkIfClusterThermostat: function (item) {
      var thermostat = false
      var clusters = item.clusters
      clusters.forEach(function (valor, indice, array) {
        if (valor.localeCompare('Thermostat') === 0) {
          thermostat = true
          return thermostat
        }
      })
      return thermostat
    },
    checkIfClusterOTA: function (item) {
      var OTA = false
      var clusters = item.clusters
      clusters.forEach(function (valor, indice, array) {
        if (valor.localeCompare('OTA') === 0) {
          OTA = true
          return OTA
        }
      })
      return OTA
    },
    checkIfIdentify: function (item) {
      var identify = false
      var clusters = item.clusters
      clusters.forEach(function (valor, indice, array) {
        if (valor.localeCompare('Identify') === 0) {
          identify = true
          return identify
        }
      })
      return identify
    }
  }
}
</script>

<style lang="scss" scoped>
.container {
  margin-bottom: 16px;
  border-radius: 0px;
}

.margin-bottom {
  margin-bottom: 16px;
}

h2 {
  margin-bottom: 5px;
}
.menu-senial {
  height: 16px;
  width: 125px;
  color: #6E767D;
  // color: rgb(0, 0, 0);
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 0.37px;
  line-height: 16px;
}
