<template>
  <div>
    <ods-row class="top-buttons">
      <ods-col :xs="12" :sm="12" :md="12" :lg="12" :xl="12" class="margin-left">
        <ods-row :gutter="10">
          <ods-col class="select" :xs="5" :sm="5" :md="5" :lg="5" :xl="5">
            <h3 class="select-text">{{$t('dialog.selectCluster')}}:</h3>
          </ods-col>
          <ods-col class="select" :xs="10" :sm="10" :md="10" :lg="10" :xl="10">
            <ods-select v-model="cluster" placeholder="Clusters" v-on:change="getdata(cluster)">
              <ods-option
                v-for="item in options"
                :key="item"
                :label="item"
                :value="item">
              </ods-option>
            </ods-select>
          </ods-col>
          <ods-col :xs="8" :sm="8" :md="8" :lg="8" :xl="8" class="margin-top-5">
            <ods-checkbox v-model="checked">{{$t('dialog.showunsupported')}}</ods-checkbox>
          </ods-col>
        </ods-row>
      </ods-col>
    </ods-row>
    <ods-row type="flex" class="top-header">
      <ods-col class="button-left" :span="24">
        <ods-button class="h3" icon="ods-icon-arrow-left" type="text" size="mini" @click="$router.go(-1)">{{$t('button.back')}}</ods-button>
      </ods-col>
      <ods-col :span="24">
        <h1 class="h1">{{$t('dialog.devsignals')}}</h1>
      </ods-col>
    </ods-row>
    <ods-row class="container">
      <ods-row>
        <ods-table
            :data="zclinfo"
            style="width: 100%"
            size="mini">
          <ods-table-column
            fixed
            prop="name"
            :label=table.att
            width="200">
          </ods-table-column>
          <ods-table-column
            prop="attribute"
            :label=table.attId
            width="120">
          </ods-table-column>
          <ods-table-column
            prop="value"
            :label=table.value
            width="120">
          </ods-table-column>
          <ods-table-column
            prop="bytes"
            :label=table.raw
            width="120">
          </ods-table-column>
          <ods-table-column
            prop="nReports"
            :label=table.report
            width="120">
          </ods-table-column>
          <ods-table-column
            prop="timestamp"
            :label=table.timestamp
            width="170">
          </ods-table-column>
          <ods-table-column
            prop="minTime2Report"
            :label=table.mintime
            width="120">
          </ods-table-column>
          <ods-table-column
            prop="maxTime2Report"
            :label=table.maxtime
            width="120">
          </ods-table-column>
          <ods-table-column
            prop="unsupported"
            :label=table.unsupported
            width="120">
          </ods-table-column>
        </ods-table>
      </ods-row>
    </ods-row>
  </div>
</template>
<script>

import axios from 'axios'

// var url = ''
export default {
  data () {
    return {
      checked: false,
      options: [],
      zclinfo: [],
      labels: {
        buttonback: this.$t('button.back'),
        buttonconfirm: this.$t('button.confirm')
      },
      table: {
        att: this.$t('table.att'),
        attId: this.$t('table.attId'),
        value: this.$t('table.value'),
        raw: this.$t('table.raw'),
        report: this.$t('table.report'),
        timestamp: this.$t('table.timestamp'),
        mintime: this.$t('table.mintime'),
        maxtime: this.$t('table.maxtime'),
        unsupported: this.$t('table.unsupported')
      },
      fixed: true,
      cluster: ''
    }
  },
  created () {
    axios.get(this.$url + '/api/zbclusters', {params: {mac: this.$route.params.mac}}).then(response => {
      this.options = response.data.data
    })
  },
  methods: {
    getdata (zcl) {
      if (this.checked) {
        axios.get(this.$url + '/api/zbgetdata', {params: {mac: this.$route.params.mac, cluster: zcl, showunsupported: 'true'}})
          .then(response => {
            this.zclinfo = response.data.data
          })
          .catch(error => {
            console.log(error)
          })
      } else {
        axios.get(this.$url + '/api/zbgetdata', {params: {mac: this.$route.params.mac, cluster: zcl}})
          .then(response => {
            this.zclinfo = response.data.data
          })
          .catch(error => {
            console.log(error)
          })
      }
    },
    handleChange (value) {
      console.log(value)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.container {
  width: 90%;
  margin-top: 2%;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 2%;
  background-color: white;
  padding: 2%;
  box-shadow: 0px 0px 5px 1px rgb(218, 218, 218);
}
.button-left {
  margin-bottom: 40px;
  margin-top: 40px;
  margin-left: -12px;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  flex-direction: row;
  flex-wrap: nowrap;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  align-items: center;
  position: relative;
  width: auto;
  height: 100%;
}
.top-header {
  display: flex;
  align-items: center;
  width: 90%;
  margin-left: auto;
  margin-right: auto;
}
.h1 {
  font-size: 22px;
  margin-bottom: 30px;
}
.h3 {
  color: #2E6C99;
  font-size: 10px;
  text-transform: uppercase;
  margin-top: -5px;
}
.top-buttons {
  border-bottom: solid;
  border-color:#D8D8D8;
  border-width: 1px;
  background-color: #FFFFFF;
  height: 72px;
  display: flex;
  align-items: center;
  width: 100%;
}
.margin-left {
  margin-left: 24px;
}
.select {
  display: flex;
  color: #888A91;
  font-size: 12px;
  // width: 70%;
}
.select-text {
  margin-top: 10px;
  // width: 80%;
}
.margin-top-5 {
  margin-top: 5px;
}
</style>



